<template>
  <div class="home">
    <!-- <banner :banners='banners' /> -->
    <top-category :typeOneList="typeOneList" />
    <banner-two :banners="banners" />
    <el-row :gutter="20">
      <el-col :span="6" v-for="(item, index) in shopList" :key="index">
        <shop @onShopClick="linkDetail" :shop="item" />
      </el-col>
    </el-row>
    <back-top></back-top>
  </div>
</template>

<script>
import Banner from "../../components/Banner.vue";
import src1 from "../../assets/images/1.jpg";
import src2 from "../../assets/images/2.jpg";
import src3 from "../../assets/images/3.jpg";
import src4 from "../../assets/images/4.jpg";
import BannerTwo from "../../components/BannerTwo.vue";
import { getShopList, getTypeOne } from "../../api/home.js";
import TopCategory from "../../components/TopCategory.vue";

let isFlag = true;

export default {
  components: { Banner, BannerTwo, TopCategory },
  name: "Home",
  data() {
    return {
      banners: [
        { src: src1, title: "第四张", id: "101010" },
        { src: src2, title: "第一张", id: "101011" },
        { src: src3, title: "第二张", id: "101012" },
        { src: src4, title: "第三张", id: "101013" },
      ],
      shopList: [],
      page: "1",
      typeOneList: [], //商品一级分类
    };
  },

  methods: {
    getShopList() {
      return getShopList(this.page).then((data) => {
        this.shopList = this.shopList.concat(data);
      });
    },
    scrollHandler() {
      console.log(1);
      let pageH = getComputedStyle(document.querySelector("#app"))["height"];
      pageH = parseInt(pageH);
      let h = innerHeight;
      let H = document.documentElement.scrollTop || document.body.scrollTop;
      if (h + H > pageH - 10 && isFlag) {
        isFlag = false;
        this.page++;
        this.getShopList().then(() => {
          isFlag = true;
        });
      }
    },
    linkDetail(item) {
      this.$router.push(`/detail/${item.Id}?type_one=${item.type_one}`);
    },
 
    // 获取一级商品分类
    getTypeOne() {
      getTypeOne().then((data) => {
        this.typeOneList = data;
      });
    },
  },
  created() {
    this.getShopList();
    this.getTypeOne();
  },
  mounted() {
    // 触底加载更多
    window.addEventListener("scroll", this.scrollHandler);
    // loadmore(this.getShopList,this.page)
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.scrollHandler);
  },
};
</script>

<style scoped lang='less'>
.home {
  width: 1200px;
  margin: 0 auto;
}
.el-row {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
}
.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}
.el-backtop {
  padding: 5px;
}
</style>